/**
 * Version    : 2.1.0
 * Author     : Geoff Thibeau
 * Author URI : https://geoffthibeau.info
*/
*
, html
, body
, th
, td {
  margin: 0;
  padding: 0;
}

*
, *:before
, *:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

article
, aside
, canvas
, details
, figcaption
, figure
, footer
, header
, hgroup
, nav
, section
, summary
, video {
  display: block;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset
, img {
  border: 0;
}

address
, caption
, cite
, code
, dfn
, em
, strong
, th
, var {
  font-style: normal;
  font-weight: normal;
}

ol
, ul
, li {
  list-style: none;
}

caption
, th {
  text-align: left;
}

h1
, h2
, h3
, h4
, h5
, h6 {
  font-size: 100%;
  font-weight: normal;
}

q:before
, q:after {
  content: '';
}

strong {
  font-weight: 700;
}

em {
  font-style: italic;
}

a img {
  border: 0;
}

* + * {
  margin-top: 1em;
}

br + br
, nav * + *
, p * + *
, table * + * {
  margin-top: auto;
}

html {
  background-color: #333;
}

body {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 62.5%;
  line-height: 1.7;
  background-color: #e9e9e5;
  color: #2a2000;
}

h1 {
  font-family: "Abril Fatface", Georgia, serif;
  font-size: 450%;
  font-weight: 400;
  line-height: 1.2;
}

h2 {
  font-size: 225%;
  font-weight: 300;
  line-height: 1.2;
}

h1 + h2 {
  margin-top: .5em;
}

h3 {
  font-size: 90%;
  letter-spacing: .1em;
  text-transform: uppercase;
}

h3
, h4 {
  font-weight: 700;
}

h5
, h6 {
  font-weight: 400;
}

h4
, h5
, h6 {
  font-size: 100%;
}

small {
  font-size: 90%;
  line-height: inherit;
}

abbr {
  font-size: 90%;
  letter-spacing: .07em;
  border-bottom: 1px dashed;
  cursor: help;
}

a {
  text-decoration: none;
  outline: none;
  color: #ff7100;
}

a:hover {
  text-decoration: underline;
}

a img {
  border: none;
}

blockquote {
  margin: 1.5em 3em 0 1em;
  padding-left: 2em;
}

table {
  width: 100%;
}

tr.odd {
  background-color: #d1d2d0;
}

tr:hover {
  background-color: #888;
  color: #fff;
}

td {
  padding: .5em;
}

li {
  margin-top: .5em;
  margin-left: 1em;
  list-style: disc;
}

.container {
  width: 100%;
  min-height: 20em;
  margin: 0 auto;
  padding: 0 .5em 1em;
  font-size: 120%;
  text-align: left;
}

@media (min-width: 30em) {
  .container {
    font-size: 140%;
  }
}

@media (min-width: 45em) {
  header {
    width: 200px;
    position: fixed;
    top: 0;
  }
}

header h3
, header a
, header .tel {
  display: block;
}

.brand {
  width: 200px;
  margin: 0;
  padding: 29px 0 6px;
  display: block;
}

.brand img {
  width: 100%;
  height: auto;
}

section
, .bio {
  max-width: 30em;
}

@media (min-width: 45em) {
  section {
    width: 30em;
    max-width: none;
    margin-top: 0;
    margin-left: 256px;
  }
}

@media (min-width: 60em) {
  section {
    width: 45em;
  }
}

.skills
, .positions {
  width: 100%;
  max-width: 30em;
  margin-left: 0;
}

@media (min-width: 60em) {
  .skills {
    width: 45em;
    max-width: none;
  }
}

.circle-text {
  width: 12em;
  height: 12em;
  margin: 1em 1em 0 0;
  padding: 2em;
  position: relative;
  display: inline-block;
  vertical-align: top;
  font-size: 100%;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1;
  text-align: center;
  border: 1px solid;
  border-color: #fff;
  border-color: rgba(255, 255, 255, 0.7);
  background-color: #fff;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 100%;
}

.circle-text.circle-text-design {
  border-color: #ff7100;
  border-color: rgba(255, 113, 0, 0.7);
  background-color: #ff7100;
  background-color: rgba(255, 113, 0, 0.7);
}

.circle-text.circle-text-development {
  border-color: #333;
  border-color: rgba(51, 51, 51, 0.7);
  background-color: #333;
  background-color: rgba(51, 51, 51, 0.7);
  color: #fff;
}

.circle-text span {
  height: 1em;
  margin: auto;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
}

.circle-text .three-line {
  bottom: 2em;
}

@media (min-width: 60em) {
  .positions
, .portfolio-item {
    width: 60em;
    max-width: none;
  }
}

.skill {
  width: auto;
  height: 3.25em;
  padding: 1em;
  border-radius: 2em;
}

.skill span {
  position: relative;
}

.portfolio-item {
  max-width: 100%;
  height: auto;
  border: 0.5em solid #fff;
  border: 0.5em solid rgba(255, 255, 255, 0.7);
  border-radius: .25em;
  box-shadow: 0 0 0 1px #fff;
}

.line-break {
  display: none;
}

footer {
  padding: 2em .5em;
  font-size: 140%;
  border-top: 0.3em solid #ff7100;
  background-color: #333;
}

footer p {
  text-align: center;
  color: #888;
}

@media print {
  .container
, footer {
    font-size: 10pt;
  }
  section {
    width: 6in;
    max-width: 100%;
    margin: 0;
  }
  .skills
, .positions {
    width: 9in;
    max-width: none;
  }
  .position:nth-child(odd) {
    margin-left: 1em;
  }
  .position:first-child {
    margin-left: 0;
  }
  .circle-text {
    border-color: #fafafa;
    background-color: #fafafa;
    -webkit-print-color-adjust: exact;
  }
  .portfolio-item {
    width: auto;
    max-width: 5in;
    height: auto;
  }
  .page-break {
    page-break-before: always;
  }
  .line-break {
    display: inline;
  }
}

/*# sourceMappingURL=style.css.map */